<template>
	<div id="order-zhunshibao">
		<public-header title="准时宝详情"></public-header>
		<div class="content">
			<div class="zhunshibao-list">
				<div class="zhunshibao-item">
					<div class="zhunshibao-item-left">价格</div>
					<div class="zhunshibao-item-right">{{Lang.dollarSign}}{{order.zhunshibao_price}}</div>
				</div>
			</div>
			<div class="blank"></div>
			<div class="zhunshibao-list">
				<div class="zhunshibao-item van-hairline--bottom">
					<div class="zhunshibao-item-left">订单号</div>
					<div class="zhunshibao-item-right">{{order.ordersn}}</div>
				</div>
				<div class="zhunshibao-item van-hairline--bottom">
					<div class="zhunshibao-item-left">服务状态</div>
					<div class="zhunshibao-item-right">{{order.status == 6 ? '已退款' : '购买成功'}}</div>
				</div>
				<div class="zhunshibao-item van-hairline--bottom align-top">
					<div class="zhunshibao-item-left">赔付标准</div>
					<div class="zhunshibao-item-right" v-if="order.zhunshibao_tips">
						<div v-for="item in order.zhunshibao_tips">{{item}}</div>
					</div>
				</div>
				<div class="zhunshibao-item flex-lr" @click="onChangePopup()">
					<div class="zhunshibao-item-left">服务说明</div>
					<div class="icon icon-right c-gray"></div>
				</div>
			</div>
			<div class="blank"></div>
			<div class="zhunshibao-list">
				<div class="zhunshibao-item van-hairline--bottom">
					<div class="zhunshibao-item-left">配送状态</div>
				</div>
				<van-steps direction="vertical" :active="0" active-color="#ff2d4b">
					<van-step v-if="order.status == 5">
						<h3 v-if="order.zhunshibao_status == 1">订单已送达，本次配送未达到赔付标准</h3>
						<h3 v-else-if="order.zhunshibao_status == 2">订单已送达，等待赔付{{order.zhunshibao_compensate}}{{Lang.dollarSignCn}}</h3>
						<h3 v-else-if="order.zhunshibao_status == 3">订单已送达，已赔付{{order.zhunshibao_compensate}}{{Lang.dollarSignCn}}</h3>
						<h3 v-else-if="order.zhunshibao_status == 4">订单已送达，达到赔付标准， 获赔{{order.zhunshibao_compensate}}{{Lang.dollarSignCn}}，平台设置为已赔付</h3>
						<p>{{order.endtime_cn}}</p>
					</van-step>
					<van-step v-else>
						<h3 v-if="order.status == 6">已退保</h3>
						<h3 v-else>准时宝生效中，订单完成后结算</h3>
					</van-step>
					<van-step>
						<h3>预计送达</h3>
						<p>{{order.prredict_time_cn}}</p>
					</van-step>
				</van-steps>
			</div>
		</div>
		<van-popup v-model="agreementShow" position="bottom" class="agreement-popup" :style="{height: '100%'}">
			<van-nav-bar class="border-0px" title="准时宝服务协议" @click-left="onChangePopup()" :style="{background: '#ff2d4b', color: '#fff' }">
				<van-icon name="left" :style="{color: '#fff'}" slot="left" class="font-20"></van-icon>
			</van-nav-bar>
			<div class="popup-content margin-10" v-html="order.zhunshibao_agreement"></div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				showPreLoading: true,
				agreementShow: false,
				order: {},
			}
		},
		components: {
			PublicHeader
		},
		methods: {
			onChangePopup() {
				this.agreementShow = !this.agreementShow;
			},
			onLoad() {
				if(this.$route.query.id) {
					this.id = this.$route.query.id;
				} else {
					this.$toast('订单不存在或已删除！')
					return;
				}
				this.util.request({
					url: 'wmall/order/index/zhunshibao',
					data: {
						id: this.id,
					}
				}).then((res) => {
					this.showPreLoading = false;
					var result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message, this.util.getUrl({path: 'pages/order/detail', query: {id: this.id}}), 3000);
						return;
					}
					this.order = result.message.order;
				})
			}
		},
		mounted () {
			this.onLoad();
		}
	}
</script>

<style>
	#order-zhunshibao .content{
		background-color: #fff;
	}
	#order-zhunshibao .blank{
		width: 100%;
		height: 10px;
		background-color: #f5f5f5;
	}
	#order-zhunshibao .zhunshibao-list{
		background-color: #fff;
		padding-left: 15px;
	}
	#order-zhunshibao .zhunshibao-list .zhunshibao-item{
		display: flex;
		align-items: center;
		padding: 10px 15px 10px 0;
	}
	#order-zhunshibao .zhunshibao-list .zhunshibao-item .zhunshibao-item-left{
		width: 35%;
		text-align: left;
		color: #999;
	}
	#order-zhunshibao .zhunshibao-list .zhunshibao-item.align-top{
		align-items: flex-start;
	}
	#order-zhunshibao .zhunshibao-list .zhunshibao-item .zhunshibao-item-right{
		font-size: 15px;
		color: #333;
	}
	#order-zhunshibao .zhunshibao-list .zhunshibao-item .zhunshibao-item-right div{
		padding-bottom: 5px;
		color: #333;
	}
	#order-zhunshibao .zhunshibao-list .zhunshibao-item .zhunshibao-item-right div:last-child{
		padding-bottom: 0;
	}
	#order-zhunshibao .van-step__circle-container .van-icon-checked{
		color: #fff!important;
		border-radius: 100%;
		border: 1px solid #ff2d4b;
		background-color: #ff2d4b;
	}
	#order-zhunshibao .agreement-popup .van-nav-bar__title{
		color: #fff!important;
	}
</style>


